<template>

  <el-row :gutter="20">
    <el-col :span="18" :offset="3" v-for="post in sortedPosts" :key="post.id">
      <el-card :body-style="{ padding: '0px' }" shadow="hover" style="martin-top: 30px; margin-bottom: 30px">
        <el-row style="padding: 14px" align="middle" justify="center">
          <el-col :xs="24" :sm="24" :md="14" :lg="16" :xl="12">
            <div class="grid-content bg-purple">
              <p class="text-center">
                <b>{{ post.title }}</b>
              </p>
              <div class="bottom">
                {{ post.summary }} ..
                <el-link :underline="false" type="primary" :href="'/blog/' + post.slug">      
                  Read more
                </el-link>
                <el-link v-if="admin" :underline="false" type="success" :href="'/blog/' + post.slug" style="margin-left: 5px">      
                  Edit post
                </el-link>
              </div>
            </div>
          </el-col>
          <el-col style="text-align:center;" :xs="24" :sm="24" :md="10" :lg="8" :xl="12">
            <img v-if="post.image && post.image != ''" style="margin-bottom:auto; " :src="post.image"/>
            <img v-else style="margin-bottom:auto; " src="https://pineco.de/wp-content/uploads/2018/10/laravel-package-development-basics.png"/>
          </el-col>
          <el-col :col="24">
              <small style="float:right">
                {{ timeConvert(post.created_at) }}
              </small>
          </el-col>
        </el-row>
      </el-card>
    </el-col>
  </el-row>
</template>

<script>
import { useLoadPosts } from '@/firebase'
import { computed } from 'vue'
export default {
  props: {
    admin: Object
  },
  setup() {
    const posts = useLoadPosts()

    const timeConvert = (timestamp) => {
      var d = new Date(timestamp.seconds * 1000)
      var curr_date = d.getDate()
      var curr_month = d.getMonth() + 1
      var curr_year = d.getFullYear()

      return curr_date + "-" + curr_month + "-" + curr_year
    }

    var sortedPosts = computed(() => {
       return posts.value.slice().sort((a, b) => {
        return new Date(b.created_at.seconds * 1000) - new Date(a.created_at.seconds * 1000)
      })
    })

    return {
      posts,
      timeConvert,
      sortedPosts
    }
  }

}
</script>

<style scoped>
img {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  width: 250px
}
</style>